import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from "../header/Header"
import Subheader from "../subHeader/subHeader"
import HomeBox from "../homeBox/HomeBox"
import PersonInfo from "./PersonInfo"
import PersonAverageInfo from "./PersonAverageInfo"
import Bargraph from "../bargraph/Bargraph"
import "./Lifespans.css"


class LifespansGraph extends Component {
	render() {
        var page = this.props.location.state.page;
        var description;
        if (page === "all"){
            description = "Death age distribution of all of your ancestors";
        } else if (page === "mother"){
            description = "Death age distribution of your ancestors on your mother's side";
        } else {
            description = "Death age distribution of your ancestors on your father's side";
        }
		return (
			<div>
                <Header name=" - Lifespans" home="true"/>		
                <Subheader familyGroup={page} linkTo = "lifespansGraph"/>
                <Bargraph title="Lifespans"
                 description={description}
                 y="Number of people"
                 x = "Age at death"
                 rowCount="9"
                 linkTo="personRows"/>
			</div>
		);
	}
}

export default LifespansGraph;