import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import './tab.scss';

class Tab extends Component {
  render() {
    if (this.props.selected == "true"){
      return (
        <Link to={{pathname: "/"+this.props.linkTo, state:{page: this.props.page}}} className="tab selectedTab disabled">
        {this.props.name}
        </Link>
    );
    } else {
      return (
        <Link to={{pathname: "/"+this.props.linkTo, state:{page: this.props.page}}} className="tab">
        {this.props.name}
        </Link>
    );
    }
   
  }
}

export default Tab;
