import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './PersonInfo.scss';

class PersonAverageInfo extends Component {
  render() {
    var page = this.props.page;
    var subTitle = "";
    if (page === "all"){
      subTitle = "Both Sides";
    } else if (page === "mother"){
      subTitle = "Mother's Side";
    } else {
      subTitle = "Father's Side";
    }
    return (
        <div className="personInfo">
            <div className="personInfo-Title">
                {this.props.title}
                <span className="lifespansSubTitle" >{subTitle}</span>
            </div>
            <div className="personInfo-Name">
                {this.props.name}
            </div>
            <div className="lifespanLinkWrapper">
                <Link to={{pathname: "/LifespansGraph", state:{page: page}}} className="textLink">View Graph</Link>
            </div>
        </div>
    );
  }
}

export default PersonAverageInfo;
