import React, { Component } from 'react';
import './PersonInfo.scss';

class PersonInfo extends Component {
  render() {
      var page = this.props.page;
      var subTitle = "";
      if (page === "all"){
        subTitle = "Both Sides";
      } else if (page === "mother"){
        subTitle = "Mother's Side";
      } else {
        subTitle = "Father's Side";
      }
    return (
        <div className="personInfo">
            <div className="personInfo-Title">
                {this.props.title} 
                <span className="lifespansSubTitle" >{subTitle}</span>
            </div>
            <div className="personInfo-Name">
                {this.props.name}
            </div>
            <div className="personInfo-Birth">
                Birth:<span className="birthDate">{this.props.birth}</span>
            </div>
            <div className="personInfo-Death">
                Death:<span className="deathDate">{this.props.death}</span>
            </div>
            <div className="personInfo-Age">
            {this.props.age}
            </div>
            <div className="personInfo-Link">
                <a href="http://familysearch.org" target="_blank">View on FamilySearch</a>
            </div>
        </div>
    );
  }
}

export default PersonInfo;
